.listing-options {
  margin-top: 1%;
  margin-left: 3%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;

  h5 {
    margin-right: 50px;
    padding: 3px 3px 0px 3px;
    color: #1A276C;
    cursor: pointer;
    font-weight: 400;
    font-size: 0.9rem;
    text-wrap: nowrap !important;
    text-align: center;
    min-width: 150px;
  }

  .listing-buttons {
    display: flex;
    flex-direction: row;
    width: 90%;
    overflow-x: scroll;
    padding-bottom: 0%;
  }

  /* Handle of the scrollbar */
  ::-webkit-scrollbar-thumb {
    background: rgba(26, 39, 108, 0.0);
    border-radius: 5px;
  }

  .logout-button {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: rgba(237, 107, 134, 0.07);
    margin-right: 4%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    .logout-icon {
      background-image: url('../../../../public/white_icons/exit.svg');
      width: 25px;
      height: 30px;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

}

.title-line {
  border: 0.5px solid rgba(26, 39, 108, 0.1);
  display: block;
  visibility: visible;
  margin-left: 3%;
  margin-right: 9%;
}

.listing-generator {
  color: #1A276C;
  margin-right: 5%;
  // margin-top: 7%;
  width: 100%;
  // margin-left: calc(220px + 5%);
  // height: 100vh;




  .basic-listing-wrapper {
    display: flex;
    flex-direction: row;
    padding-bottom: 5%;
    margin-left: 3%;

    margin-top: 5%;
    width: 90%;
    box-shadow: 1px 2px 1px 0 rgba(0, 0, 0, 0.1), -1px 2px 1px 0 rgba(0, 0, 0, 0.1);
    border-radius: 10px;

    .input-block.medium {
      width: 300px;
    }

    .input-block.small {
      width: 150px;
      margin-right: 15px;

    }

    .input-block.large {
      // width: 465px;
    }

    .property-insight-inputs {
      width: 465px;
    }

  }

  .property-insight-inputs {
    margin-top: 1%;
    margin-right: 3%;

    padding-left: 5%;


    .insight-title {
      font-size: 0.8rem;
      color: #1A276C;
      font-weight: 600;
      margin-top: 2%;
      margin-bottom: 1%;
    }


    .double-input-block {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-top: 3%;
      margin-bottom: 3%;
      // margin-right: 3%;




    }

    .single-input-block {
      margin-bottom: 3%;
    }

    .input-block {
      display: flex;
      flex-direction: column;

      h3 {
        color: #1A276C;
        font-weight: 600;
        font-size: 0.8rem;
        margin-bottom: 10px
      }

      input,
      select,
      textarea {
        padding: 7.5px;
        border-radius: 7.5px;
        border: 1px solid #1A276C;
        font-size: 0.8rem;
      }

      select {
        width: 100%;
      }
    }

    .search-section,
    .listing-search-section {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;

      .load-insights {
        width: 175px;
        padding: 7.5px;
        border-radius: 7.5px;
        background-color: #ED6B86;
        border: 1px solid #ED6B86;
        color: #FDF7F0;
        font-weight: 700;
        cursor: pointer;
      }
    }

    .listing-search-section {
      margin-top: 1%;
      margin-right: 3%;
    }

    .search-section {
      margin-top: 7.5%;
    }

    .lifestyle-input-block {
      width: 100%;

      .lifestyle-input-wrap {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        margin-right: 0% !important;

        .input-block-icons:nth-child(odd) {
          margin-right: 5%;
        }

        .input-block-icons {
          width: 47.5%;
          display: flex;
          flex-direction: row;
          align-items: center;
          // justify-content: space-between;
          margin-top: 2%;

          h3 {
            font-size: 0.65rem;
            text-align: left;
            width: 60%;
            font-weight: 600;
          }

          .react-switch-bg {
            width: 40px !important;
            height: 15px !important;
            // left: 5px;


          }

          .react-switch-bg div {
            height: 15px !important;
            width: 40px !important;
          }

          .react-switch-handle {
            height: 13px !important;
            width: 13px !important;
            // left: 5px;


          }
        }
      }

      .phrase-line {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin-top: 2%;
        margin-bottom: 2%;

        input {
          padding: 7.5px;
          border-radius: 7.5px;
          border: 1px solid #1A276C;
          font-size: 0.8rem;
          width: 82%;
        }

        button {
          padding: 7.5px;
          border-radius: 7.5px;
          width: 8%;
          color: #FDF7F0;
          font-size: 0.8rem;
          font-weight: 700;



        }

        .minus {
          background-color: #1A276C;
          border: 1px solid #1A276C;
        }

        .plus {
          background-color: #ED6B86;
          border: 1px solid #ED6B86;
        }
      }
    }

    .lifestyle-input-block:nth-of-type(odd) {
      margin-bottom: 10%;
    }

  }

  .full-listing-wrapper {
    display: flex;
    flex-direction: row;

    .input-block.medium {
      width: 65%;
    }

    .input-block.small {
      width: 35%;
      // margin-right: 15px;

    }

    .input-block.large {
      width: 100%;
    }

    .input-block.half {
      width: 50%;
    }

    .input-block:first-of-type {
      margin-right: 7.5px;
    }

    .input-block:nth-of-type(2) {
      margin-left: 7.5px;
    }



    .feature-input-block {
      margin-top: 10%;
      margin-bottom: 10%;

      h3 {
        color: #1A276C;
        font-weight: 600;
        font-size: 0.8rem;
        margin-bottom: 10px
      }

      .feature-section {
        display: flex;
        flex-wrap: wrap;

        .feature {
          width: 33.33%;
          margin-top: 1%;
          margin-bottom: 1%;

          .custom-checkbox {
            position: relative;
            font-size: 0.7rem;

            .checkbox {
              position: absolute;
              opacity: 0;
              width: 100%;
              height: 100%;
              cursor: pointer;
              z-index: 2;

            }

            .label {
              position: relative;
              display: flex;
              flex-direction: row;
              align-items: center;
              color: #1A276C;
              font-weight: 500;

              &::before {
                content: "";
                display: inline-block;
                width: 12px;
                height: 12px;
                border: 2px solid rgba(26, 39, 108, 0.15);
                border-radius: 3px;
                background-color: #FFF;
                margin-right: 5px;
                transition: background-color 0.3s;
              }
            }
          }
        }

        .checkbox:checked~.label::before {
          background-color: #ED6B86;
        }
      }


    }

    .full-listing-inputs,
    .ai-listing-inputs {
      display: flex;
      flex-direction: row;
      padding-bottom: 1.5%;
      margin-left: 3%;

      margin-top: 1%;
      width: 42%;
      box-shadow: 1px 2px 1px 0 rgba(0, 0, 0, 0.1), -1px 2px 1px 0 rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      margin-right: 1.5%;
    }

    .ai-listing-outputs {
      max-height: 1300px !important;
    }


    .loading-gif {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }

    .full-listing-outputs,
    .ai-listing-outputs {
      margin-top: 1%;
      width: 50%;
      box-shadow: 1px 2px 1px 0 rgba(0, 0, 0, 0.1), -1px 2px 1px 0 rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      overflow-y: auto;
      max-height: 625px;


      .results-header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-left: 5%;
        padding-right: 5%;
        margin-top: 3%;

        .header-text {
          .results-title {
            font-size: 1.1rem;
            font-weight: 600;
          }

        }


      }

      .results-description {
        margin-bottom: 3%;

        h3 {
          font-size: 0.7rem;
          font-weight: 400;
          color: black;
        }
      }


      .results-section-icons {
        display: none;
      }

      .results-section {
        padding-left: 5%;
        padding-right: 5%;
        margin-top: 1%;
        height: 100%;

        .results-sub-title {
          margin-bottom: 2%;
          font-size: 0.9rem;
          color: black;
          font-weight: 600;
          margin-bottom: 2%;
        }

        .results-block {
          .result-block-header {
            display: flex;
            flex-direction: row;

            h3 {
              font-size: 0.9rem;
              font-weight: 600;
            }
          }




        }


        .results-details {
          padding-left: 0px;
          margin-left: 5%;

          li {
            font-size: 0.7rem;
            color: black;
            margin-top: 1%;
            margin-bottom: 1%;
            padding-left: 5%;
          }

        }

        .results-divider {
          border: 0.5px solid grey;
          visibility: visible;
          display: block;
          opacity: 0.2;
          margin-top: 3.5%;
          margin-bottom: 3.5%;
        }
      }

      .description-block {
        margin-bottom: 7%;
        margin-top: 2%;

        h1 {
          font-size: 0.9rem;
          color: black;
          font-weight: 600;
          margin-bottom: 1%;
        }

        .info-block {
          display: flex;
          flex-direction: row;
          margin-top: 1%;
          margin-bottom: 1%;

          h5 {
            width: 100px;
            color: black;
            font-weight: 500;
            font-size: 0.8rem;
          }

          h6 {
            width: 100px;
            color: #1A276C;
            font-size: 0.8rem;
            font-weight: 600;

          }
        }

        .results-text {
          font-size: 0.7rem;
          color: black;
        }

        .results-text:first-of-type {
          margin-bottom: 2%;
        }

      }
    }
  }

  .lifestyle-icon {
    width: 25px;
    height: 25px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    margin-right: 5%;
  }

}

.saved-listings {
  box-shadow: 1px 2px 1px 0 rgba(0, 0, 0, 0.1), -1px 2px 1px 0 rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-left: 3%;
  margin-right: 3%;

  .favourite-count {
    margin-top: 2%;
    margin-bottom: 2%;
    margin-left: 3%;
    margin-right: 3%;
    padding-top: 2%;
    color: black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    h3 {
      font-size: 0.8rem;
      font-weight: 700;
      color: #1A276C;
    }

    a {
      font-size: 0.9rem;
      color: #ED6B86;
      font-weight: 700;
    }
  }


  .table-section {
    margin-right: 2%;
    margin-left: 3%;
    padding-bottom: 3%;

    .property-divider {
      visibility: visible;
      margin-top: 1%;
      margin-bottom: 1%;
      border: 0.5px solid rgba(5, 24, 133, 0.15);
    }

    .property-divider:last-of-type {
      visibility: hidden;

    }

    .table-headers {
      display: flex;
      flex-direction: row;
      margin-top: 1%;
      color: rgba(5, 24, 133, 0.5);

      h5 {
        font-size: 0.75rem;
      }
    }

    .table-details {
      overflow: auto;
      max-height: 62vh;

      .results-content {
        display: flex;
        flex-direction: row;



        cursor: pointer;

        .column {
          h5 {
            font-weight: 500;
            font-size: 0.65rem;
            color: rgba(0, 0, 0, 1);
          }
        }
      }
    }

    #column1 {
      width: 5%;
    }

    #column2 {
      width: 35%;
    }

    #column3 {
      width: 20%;
    }

    #column4 {
      width: 20%;
    }

    #column5 {
      width: 15%;
    }

    #column6 {
      width: 5%;
      text-align: center;
    }
  }


}

.ai-loading-status {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 10%;

  .loading-stage {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10%;

    h3 {
      font-size: 0.8rem;
      margin-top: 5%;
    }

    p {
      font-size: 0.8rem;
      margin-top: 2%;
    }

    .loading-icon {
      width: 50px;
      height: 50px;
      border-radius: 25px;
      background-position: center;
      background-size: 70% 70%;
      background-repeat: no-repeat;
    }
  }

  .new-loading-gif {
    width: 50px;
    height: 50px;

    img {
      height: 100%;
      width: 100%;
    }
  }
}

.header-cta {
  .copy-button {
    display: flex;
    flex-direction: row;
    border: 2px solid rgba(26, 39, 108, 0.1);
    border-radius: 12px;
    padding: 10px;
    cursor: pointer;
  }

  .copy-icon {
    background-image: url('../../../../public/white_icons/Copy.svg');
    width: 20px;
    height: 20px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    margin-right: 10px;
  }

  .export-icon {
    background-image: url('../../../../public/white_icons/export.png');
    width: 20px;
    height: 20px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    margin-right: 10px;
  }

  h3 {
    font-size: 0.9rem;
    color: #ED6B86;
    // opacity: 1;
    font-weight: 600;
  }
}

