.no-access-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 70vh;

  .no-access-image {
    background-image: url('../../../../public/website_icons/Dead_end.svg');
    width: 350px;
    height: 210px;
    background-position: center;
    background-size: 90% 90%;
    background-repeat: no-repeat;
    margin-bottom: 2%;
  }

  .no-access-title {
    font-size: 2.5rem;
    margin-bottom: 2%;
    font-weight: 500;
  }

  .no-access-message {
    padding-left: 10%;
    padding-right: 10%;
    font-size: 0.8rem;
    text-align: center;
    font-weight: 400;
    margin-bottom: 2%;

  }

  .no-access-email {
    .email-button {
      width: 125px;
      padding: 5px;
      background-color: #ED6B86;
      border: 1px solid #ED6B86;
      color: #FDF7F0;
      border-radius: 5px;
      font-weight: 600;
      cursor: pointer;
    }
  }
}


@media screen and (max-width: 1400px) {
  .listing-options {
    max-width: 100%;

    .listing-buttons {
      width: 95%;
      overflow: scroll;
    }

  }

  .title-line {
    margin-top: 0% !important;
  }

}



@media screen and (max-width: 1100px) {


  .full-listing-inputs,
  .ai-listing-inputs {
    width: 50% !important;
  }

  .full-listing-outputs,
  .ai-listing-outputs {
    width: 45% !important;
  }

  .logout-button {
    display: none !important;
  }
}


@media screen and (max-width: 900px) {
  .listing-options {
    margin-top: 2% !important;

  }

  .insights-results-wrapper {
    margin-top: 1% !important;
  }

  .listing-generator .full-listing-wrapper {
    flex-direction: column !important;

    .full-listing-inputs,
    .ai-listing-inputs {
      width: 95% !important;
    }

    .full-listing-outputs,
    .ai-listing-outputs {
      margin-bottom: 2% !important;
      margin-left: 3% !important;
      padding-left: 5% !important;
      width: 95% !important;
    }
  }
}




@media screen and (max-width: 600px) {

  .basic-listing-wrapper,
  .full-listing-wrapper {
    .property-insight-inputs {
      width: 95% !important;

      .double-input-block {
        width: 95% !important;
      }

      .input-block.medium {
        width: 60% !important
      }

      .input-block.large {
        width: 95% !important
      }
    }

    .search-section {
      width: 95% !important;
    }
  }


  .full-listing-outputs {
    margin-top: 10% !important;
  }

  .full-listing-inputs {
    padding-bottom: 5% !important;
  }

  .table-section {
    overflow: auto;

    .property-divider {
      width: 145% !important;
    }

    #column1 {
      min-width: 5%;
    }

    #column2 {
      min-width: 50%;
    }

    #column3 {
      min-width: 30%;
    }

    #column4 {
      min-width: 30%;
    }

    #column5 {
      min-width: 20%;
    }

    #column6 {
      min-width: 10%;
      text-align: center;
    }
  }

  .table-details {
    overflow: visible !important;
  }

}

@media screen and (max-width: 450px) {
  .basic-listing-wrapper {
    .property-insight-inputs {
      width: 90% !important;

      .double-input-block {
        width: 100% !important;
      }

      .input-block.medium {
        width: 48% !important;
      }

      .input-block.small {
        width: 50% !important;
        margin-right: 2% !important
      }

      .input-block.large {
        width: 100% !important;
      }
    }

    // .search-section {
    //   width: 280px !important;
    // }
  }

}